import React, { useState, useEffect } from "react";
import "./App.css";
import icon from "./icon.png";

function App() {
  const [datamuse, setData] = useState([]);
  const [phon, setPhon] = useState();
  const [defI, setI] = useState();
  const [def, setDef] = useState([]);
  const [ex, setEx] = useState([]);
  const [hl, setHl] = useState(JSON.parse(localStorage.getItem("hl")) || []);
  const [searchInput, setSearch] = useState("");
  const [sortZA, setSort] = useState(false);
  const [input, setInput] = useState("");
  const [modInput, setMod] = useState("");
  const [vocList, setList] = useState(
    JSON.parse(localStorage.getItem("words")) || []
  );

  useEffect(() => {
    localStorage.setItem("words", JSON.stringify(vocList));
    localStorage.setItem("hl", JSON.stringify(hl));
  }, [vocList, hl]);

  useEffect(() => {
    fetch(`https://api.datamuse.com/sug?s=${input}&max=5`)
      .then(res => res.json())
      .then(data => setData(data));
  }, [input]);

  function addWord(e) {
    e.preventDefault();
    input.split(", ").forEach(word => {
      if (vocList.indexOf(word) >= 0) {
        return alert(`"${word}" is already in the vocabulary list`);
      } else {
        // setHl(prevHl => [...prevHl, false]);
        setList(prevList => [...prevList, word]);
      }
      setInput("");
    });
  }

  function download(filename, text) {
    var element = document.createElement("a");
    element.setAttribute(
      "href",
      "data:text/plain;charset=utf-8," + encodeURIComponent(text)
    );
    element.setAttribute("download", filename);

    element.style.display = "none";
    document.body.appendChild(element);

    element.click();

    document.body.removeChild(element);
  }

  // Start file download.
  function handleDownload() {
    // Generate download of hello.txt file with some content
    var text = String(vocList);
    var filename = "vocabList.txt";

    download(filename, text);
  }

  function deleteWord(index) {
    setList(prevList => prevList.filter((_, i) => i != index));
    // setHl(prevHl => prevHl.filter((_, i) => i != index));
  }

  function modifyWord(e, index) {
    e.preventDefault();
    setMod("");
    if (vocList.indexOf(modInput) >= 0) {
      return alert(`"${modInput}" is already in the vocabulary list`);
    } else {
      deleteWord(index);
      setList(prevList =>
        [...prevList, modInput].sort((a, b) => {
          if (!sortZA) {
            return a.localeCompare(b, "en", { sensitivity: "base" });
          } else {
            return a.localeCompare(b, "en", { sensitivity: "base" }) == 1
              ? -1
              : a.localeCompare(b, "en", { sensitivity: "base" }) == -1
              ? 1
              : 0;
          }
        })
      );
      // setHl(prevHl => [...prevHl, false]);

      toggle(index);
    }
  }

  function toggle(index) {
    let display = document.getElementById("modify" + String(index)).style
      .display;
    if (display == "none") {
      document.getElementById("modify" + String(index)).style.display = "block";
    } else if (display == "block") {
      document.getElementById("modify" + String(index)).style.display = "none";
    }
  }

  function toggleInput() {
    document.querySelector(".search-input").classList.toggle("active");
  }

  function handleMouseEnter() {
    document.getElementById("1").classList.add("hover");
  }

  function handleMouseLeave() {
    document.getElementById("1").classList.remove("hover");
  }

  function sortA() {
    setSort(false);
  }

  function sortZ() {
    setSort(true);
  }

  function highlightWord(word, index) {
    if (hl.includes(word)) {
      setHl(prevHl => [...prevHl].filter(vocab => vocab != word));
      document.querySelector(`.hl${index}`).style.background = "none";
    } else {
      setHl(prevHl => [...prevHl, word]);
    }
  }

  function wordDef(word, index) {
    if (defI == index) {
      setI();
      document.getElementById(`defbtn${index}`).style.backgroundColor = "white";
      document.getElementById(`defbtn${index}`).style.color = "rgb(0, 49, 128)";
      document.getElementById(`defbtn${index}`).textContent = "▼";
    } else {
      fetch(
        `https://googledictionaryapi.eu-gb.mybluemix.net/?define=${word}&lang=en`
      )
        .then(res => res.json())
        .then(data => {
          let defs = Object.values(data[0].meaning);
          setDef(
            defs.map(def => {
              console.log("rendered");
              return def[0].definition ? (
                <li>
                  <i>{def[0].definition}</i>
                </li>
              ) : null;
            })
          );
          setEx(
            defs.map(def => {
              console.log("rendered");
              return def[0].definition ? <li>"{def[0].example}"</li> : null;
            })
          );
          setPhon(data[0].phonetic);
          setI(index);
          document.getElementById(`defbtn${index}`).style.backgroundColor =
            "rgb(0, 49, 128)";
          document.getElementById(`defbtn${index}`).style.color = "white";
          document.getElementById(`defbtn${index}`).textContent = "▲";
        })
        .catch(err => {
          setPhon("No Definition Found :(");
          setI(index);
          document.getElementById(`defbtn${index}`).textContent = "x";
        });
      document.getElementById(`defbtn${index}`).textContent = "⏳";
    }
  }

  function handleShwbtn(index) {
    document.getElementById(`defbtn${index}`).style.opacity = "1";
    document.getElementById(`defbtn${index}`).style.pointerEvents = "initial";
  }

  function handleHdbtn(index) {
    if (defI == index) return;
    document.getElementById(`defbtn${index}`).style.opacity = "0";
    document.getElementById(`defbtn${index}`).style.pointerEvents = "none";
  }

  return (
    <div className="App">
      <header className="app-header">
        <h1>Aurore English Vocabulary App</h1>
      </header>
      <div id="1" className="dwn-text">
        Download Current List
      </div>
      <button
        className="dwn-btn"
        onClick={handleDownload}
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
      >
        <img src={icon} width="20px" />
      </button>
      <form onSubmit={addWord} className="add-form">
        <input
          className="add-btn"
          type="text"
          value={input}
          onChange={e => setInput(e.target.value)}
          placeholder="Type word or list of words here..."
          required
        />

        {input ? (
          <ul className="datamuse-res">
            {datamuse.map(res => (
              <li onClick={() => setInput(res.word)}>{res.word}</li>
            ))}
          </ul>
        ) : null}

        <button>Add Word</button>
      </form>
      <div className="sort-container">
        Sort:{" "}
        <a className="link" href="#" onClick={sortA}>
          A-Z
        </a>{" "}
        |{" "}
        <a className="link" href="#" onClick={sortZ}>
          Z-A
        </a>{" "}
        |{" "}
        <a className="link" href="#" onClick={toggleInput}>
          Search
        </a>
        <input
          className="search-input"
          type="text"
          value={searchInput}
          onChange={e => setSearch(e.target.value)}
        ></input>
      </div>
      <div className="list">
        {vocList.length ? (
          vocList
            .sort((a, b) => {
              if (!sortZA) {
                return a.localeCompare(b, "en", { sensitivity: "base" });
              } else {
                return a.localeCompare(b, "en", { sensitivity: "base" }) == 1
                  ? -1
                  : a.localeCompare(b, "en", { sensitivity: "base" }) == -1
                  ? 1
                  : 0;
              }
            })
            .filter(word =>
              searchInput.length
                ? word.toLowerCase().includes(searchInput.toLowerCase())
                : true
            )
            .map((word, i) => {
              return (
                <>
                  <div
                    onMouseEnter={() => handleShwbtn(i)}
                    onMouseLeave={() => handleHdbtn(i)}
                    key={i}
                    className="list-item"
                  >
                    <button
                      id={`defbtn${i}`}
                      className="show-def"
                      onClick={() => wordDef(word, i)}
                    >
                      ▼
                    </button>
                    <h2 onClick={() => wordDef(word, i)}>
                      <span
                        style={
                          hl.includes(word)
                            ? { background: "rgb(255, 217, 0)" }
                            : { background: "none" }
                        }
                        className={"hl" + i}
                      >
                        {i + 1}. {word}
                      </span>
                      {defI == i ? (
                        <i
                          style={{
                            fontSize: 14,
                            fontWeight: 400,
                            paddingLeft: 5
                          }}
                        >
                          {phon}
                        </i>
                      ) : null}
                    </h2>

                    <button
                      onClick={() => toggle(i)}
                      className="toggle-mod-btn"
                    >
                      Modify
                    </button>
                    <button
                      className="hl-btn"
                      key={i}
                      onClick={() => highlightWord(word, i)}
                    >
                      Highlight
                    </button>
                    <button onClick={() => deleteWord(i)} className="del-btn">
                      Delete
                    </button>
                  </div>
                  <div>
                    <form
                      onSubmit={e => modifyWord(e, i)}
                      style={{ display: "none" }}
                      id={"modify" + String(i)}
                    >
                      <input
                        className="toggle-mod-input"
                        type="text"
                        value={modInput}
                        onChange={e => setMod(e.target.value)}
                        placeholder="Modify vocab"
                      />
                      <button className="mod-btn">Submit</button>
                    </form>
                  </div>
                  {defI == i && phon != "No Definition Found :(" ? (
                    <div className="def-container">
                      <h3>
                        {def.length == 1
                          ? "Definition:"
                          : def.length > 1
                          ? "Definitions:"
                          : null}
                      </h3>
                      <ol>{defI == i ? def : null}</ol>
                      <h3>
                        {ex.length == 1
                          ? "Example:"
                          : ex.length > 1
                          ? "Examples:"
                          : null}
                      </h3>
                      <ol>{ex ? ex : null}</ol>
                    </div>
                  ) : null}
                </>
              );
            })
        ) : (
          <div className="novocy">No Vocabulary Yet</div>
        )}
      </div>
      <footer>
        Autocomplete powered by:{" "}
        <a href="https://www.datamuse.com/api/">Datamuse API</a>; dictionnary
        powered by:{" "}
        <a href="https://googledictionaryapi.eu-gb.mybluemix.net/">
          (unofficial) Google Dictionary API
        </a>
      </footer>
    </div>
  );
}

export default App;
